<template>
	<div class="case-box html2-canvas">
		<h1>html2Canvas </h1>
		<div class="scene"></div>
		<ul>
			<li>1.</li>
			<li>2.</li>
		</ul>
		<p class="mt20 mb20">html2canvas html2canvasCanvas,</p>
		<div id="sharePic" class="share-box">
			<img :src="img3" width="100%">
			<p class="txt" v-text="textValue"></p>
			<p class="txt1"></p>
		</div>
		<div class="share-btn">
			<el-input v-model="textValue" size="mini" placeholder="" maxlength="6" clearable></el-input>
			<el-button type="primary" size="mini" @click="downloadPic"></el-button>
		</div>
		<p class="mt80"></p>
		<div class="eg-box">
<pre>
getSharePic () {
	let _this = this
	// DIV
	let shareBox = document.getElementById('sharePic')
	let width = shareBox.offsetWidth
	let height = shareBox.offsetHeight
	// let offsetTop = 0
	let offsetTop = shareBox.offsetTop
	let canvas = document.createElement("canvas")
	let context = canvas.getContext("2d")
	let scaleBy = Math.ceil(window.devicePixelRatio)
	canvas.width = width * scaleBy
	canvas.height = (height + offsetTop) * scaleBy

	let opts = {
		useCORS: true, //
		tainttest: true, // 
		scale: scaleBy, // scale 
		canvas: canvas, //  canvas
		logging: false, // false
		width: width, // DOM 
		height: height // DOM 
	}
	// DOM
	html2canvas(shareBox, opts).then(function(canvas) {
		let fileName = "" + ".jpg"
		let picUrl = canvas.toDataURL("image/jpeg",2)
		// 
		_this.fileDown(picUrl, fileName)
	})
},
fileDown (url, name) {
	let fileName = name
	axios({
		method: "post",
		url: url,
		responseType: "blob",
	}).then(data => {
		let blob = new Blob([data.data], { type: "application/json" })
		if (window.navigator.msSaveOrOpenBlob) {
			// msSaveOrOpenBlobbool
			navigator.msSaveBlob(blob); //
		} else {
			let link = document.createElement("a") // a
			link.href = window.URL.createObjectURL(blob)
			link.download = fileName
			link.click()
			window.URL.revokeObjectURL(link.href)
		}
	})
}
</pre>
		</div>
	</div>
</template>

<script>
import axios from 'axios'
import html2canvas from 'html2canvas'

export default {
	name: 'html2-canvas',
	data() {
		return {
			textValue: '',
			shareImg: '',
			img3: require('@/assets/common/img/soccer/3.jpg'),
		}
	},
	methods: {
		downloadPic () {
			if (this.textValue !== '') {
				this.getSharePic()
			} else {
				this.$message.warning('')
			}
		},
		getSharePic () {
      let _this = this
      // DIV
      let shareBox = document.getElementById('sharePic')
			let width = shareBox.offsetWidth
			// let height = shareBox.offsetHeight
      let height = 190 * 2
      let offsetTop = shareBox.offsetTop
      let canvas = document.createElement("canvas")
      let context = canvas.getContext("2d")
      let scaleBy = Math.ceil(window.devicePixelRatio)
      canvas.width = width * scaleBy
      // canvas.height = (height + offsetTop) * scaleBy
      canvas.height = 190 * 2

      let opts = {
        useCORS: true, //
        tainttest: true, // 
        scale: scaleBy, // scale 
        canvas: canvas, //  canvas
        logging: false, // false
        width: width, // DOM 
        height: height // DOM 
      }
      // DOM
      html2canvas(shareBox, opts).then(function(canvas) {
				let fileName = "" + ".jpg"
				let picUrl = canvas.toDataURL("image/jpeg",2)
				// 
				_this.fileDown(picUrl, fileName);
      })
    },
		fileDown (url, name) {
			let fileName = name
			axios({
				method: "post",
				url: url,
				responseType: "blob",
			}).then(data => {
				let blob = new Blob([data.data], { type: "application/json" });
				if (window.navigator.msSaveOrOpenBlob) {
					// msSaveOrOpenBlobbool
					navigator.msSaveBlob(blob); //
				} else {
					let link = document.createElement("a"); //a
					link.href = window.URL.createObjectURL(blob)
					link.download = fileName;
					link.click();
					window.URL.revokeObjectURL(link.href)
				}
			})
		}
	}
};
</script>
<style lang="less" scoped>
.html2-canvas {
	.share-box {
		position: relative;
		width: 260px;
		margin-bottom: 10px;
		text-align: center;
		img {
			border-radius: 20px;
			overflow: hidden;
		}
		.txt {
			position: absolute;
			bottom: 110px;
			left: 0;
			right: 0;
			z-index: 9;
			font-size: 22px;
      color: #fff;
      font-weight: bold;
		}
		.txt1 {
			position: absolute;
			bottom: 60px;
			left: 0;
			right: 0;
			z-index: 9;
			font-size: 18px;
      color: #fff;
      font-weight: bold;
		}
	}
	.share-btn {
		.el-input {
			width: 200px;
			margin-right: 20px;
		}
	}
}
</style>
